import { productStore } from "../../store/productStore"
import { observer } from "mobx-react-lite";
import { Button, Card, DatePicker, Descriptions, Divider, Image, Input, InputNumber, List, Radio, Space } from "antd";
import { useEffect, useState } from "react";
import dayjs from 'dayjs';



function StudioPanel(){


    const {studio701Products, updateStudioDetail} = productStore;

    useEffect(
        ()=>{
            // load on init
            productStore.get701Products();

            // load on following action
            let intRefresh = setInterval(
                ()=> productStore.get701Products(), 30000
            )
            
            return ()=> clearInterval(intRefresh)
        
        },[]
    );


    //
    const updateDate =(productId, value)=>{
        value && (
            updateStudioDetail(productId,{manufactureDate: value.unix()*1000})
        )
    }

    //
    const updateCost =(productId, cost)=>{
        cost && (
            updateStudioDetail(productId,{cost})
        )
    }


    const getUserList = ()=>{
        let idSet = new Set();
        let userList = [];
        
        studio701Products.forEach(element => {
            if(idSet.has(element.agentId))
                return;
            idSet.add(element.agentId)
            userList.push({value: element.agentId, label: element.agentFullname})
        })


        // console.log(userList)
        return userList;
    }
    //
    const userList = getUserList();

    // 
    const [radioValue, setRadioValue] = useState();
    const radioChange = ({target: {value}}) => {
        setRadioValue(value)
    }
    
    const renderContent = (item)=>{
  
        return <List.Item>

        
            <Space align="top">
            <Card>
                <Image
                    width={120}
                    height={171}
                    src={`${process.env.REACT_APP_BE_IMAGE_API}${item.image}`}
                    fallback="data:image/png;base64,"
                />
            </Card>
            <Card>
                <Descriptions column={1}>
                    <Descriptions.Item label='所属'>{item.agentFullname}</Descriptions.Item>
                    <Descriptions.Item label='款式'>{item.style}</Descriptions.Item>
                    <Descriptions.Item label='编号'>{item.model}</Descriptions.Item>
                    <Descriptions.Item label='用时'><InputNumber value={item?.sampleOrder?.cost} min={-1} onChange={value => updateCost(item.id, value)} /> </Descriptions.Item>
                    <Descriptions.Item label='打板日期'><DatePicker value={dayjs(item?.sampleOrder?.manufactureDate)} inputReadOnly  onChange={(value)=>updateDate(item.id, value)}/></Descriptions.Item>
                </Descriptions>
            </Card>
            </Space>
        </List.Item>
    }

    return (

        <>
        <br />
        <Button block>dfsdfsf</Button>
        <Space >
        <Button block>dfsdfsf</Button>
        <Button>df</Button>
        <Button>df</Button>
        <Button>df</Button>
        <Button>df</Button>
        </Space>

        <Space direction='vertical' style={{display: 'flex', alignItems: 'center'}}>
            <Card>
            <Radio.Group  
                
                style={{width: '100%'}}
                options={userList}
                onChange={radioChange}
                value={radioValue}
                // optionType="button"
                buttonStyle="solid"
            />
            </Card>
        
            <List 
                dataSource={studio701Products.filter(item=>item.agentId === radioValue).sort((a,b)=>b.cdate -a.cdate)}
                renderItem={renderContent}
            />
        </Space>
        </>
    )
}


export default observer(StudioPanel)